.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__product-name {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.card__stars-n-reviews {
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  flex-direction: column;
  justify-content: flex-end;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 24px;
  margin-bottom: 0;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star:last-child {
  margin-right: 0;
}

* {
  box-sizing: border-box;
}

.page {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

